<h1 id="模板消息" tabindex="-1"><a class="header-anchor" href="#模板消息" aria-hidden="true">#</a> 模板消息</h1>
<p>模板消息仅用于公众号向用户发送重要的服务通知，只能用于符合其要求的服务场景中，如信用卡刷卡通知，商品购买成功通知等。不支持广告等营销类消息以及其它所有可能对用户造成骚扰的消息。</p>
<h2 id="获取实例" tabindex="-1"><a class="header-anchor" href="#获取实例" aria-hidden="true">#</a> 获取实例</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token php language-php"><span class="token delimiter important">&lt;?php</span>
<span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Foundation<span class="token punctuation">\</span>Application</span><span class="token punctuation">;</span>
<span class="token comment">// ...</span>
<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Application</span><span class="token punctuation">(</span><span class="token variable">$options</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$notice</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">notice</span><span class="token punctuation">;</span>
</span></code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h3 id="api" tabindex="-1"><a class="header-anchor" href="#api" aria-hidden="true">#</a> API</h3>
<ul>
<li><code>boolean setIndustry($industryId1, $industryId2)</code> 修改账号所属行业；</li>
<li><code>array getIndustry()</code> 返回所有支持的行业列表，用于做下拉选择行业可视化更新；</li>
<li><code>string  addTemplate($shortId)</code> 添加模板并获取模板ID；</li>
<li><code>collection send($message)</code> 发送模板消息, 返回消息ID；</li>
<li><code>array  getPrivateTemplates()</code> 获取所有模板列表；</li>
<li><code>array  deletePrivateTemplate($templateId)</code> 删除指定ID的模板。</li>
</ul>
<p>非链接调用方法：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$messageId</span> <span class="token operator">=</span> <span class="token variable">$notice</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'touser'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'user-openid'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'template_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'template-id'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxx'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'data'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
            <span class="token comment">//...</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><p>链式调用方法:</p>
<pre><code>设置模板ID：template / templateId / uses
设置接收者openId: to / receiver
设置详情链接：url / link / linkTo
设置模板数据：data / with

以上方法都支持 `withXXX` 与 `andXXX` 形式链式调用
</code></pre>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$messageId</span> <span class="token operator">=</span> <span class="token variable">$notice</span><span class="token operator">-></span><span class="token function">to</span><span class="token punctuation">(</span><span class="token variable">$userOpenId</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">uses</span><span class="token punctuation">(</span><span class="token variable">$templateId</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">andUrl</span><span class="token punctuation">(</span><span class="token variable">$url</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">data</span><span class="token punctuation">(</span><span class="token variable">$data</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">// 或者</span>
<span class="token variable">$messageId</span> <span class="token operator">=</span> <span class="token variable">$notice</span><span class="token operator">-></span><span class="token function">to</span><span class="token punctuation">(</span><span class="token variable">$userOpenId</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">url</span><span class="token punctuation">(</span><span class="token variable">$url</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">template</span><span class="token punctuation">(</span><span class="token variable">$templateId</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">andData</span><span class="token punctuation">(</span><span class="token variable">$data</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">// 或者</span>
<span class="token variable">$messageId</span> <span class="token operator">=</span> <span class="token variable">$notice</span><span class="token operator">-></span><span class="token function">withTo</span><span class="token punctuation">(</span><span class="token variable">$userOpenId</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">withUrl</span><span class="token punctuation">(</span><span class="token variable">$url</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">withTemplate</span><span class="token punctuation">(</span><span class="token variable">$templateId</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">withData</span><span class="token punctuation">(</span><span class="token variable">$data</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">// 或者</span>
<span class="token variable">$messageId</span> <span class="token operator">=</span> <span class="token variable">$notice</span><span class="token operator">-></span><span class="token function">to</span><span class="token punctuation">(</span><span class="token variable">$userOpenId</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">url</span><span class="token punctuation">(</span><span class="token variable">$url</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">withTemplateId</span><span class="token punctuation">(</span><span class="token variable">$templateId</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">// ... ...</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h2 id="示例" tabindex="-1"><a class="header-anchor" href="#示例" aria-hidden="true">#</a> 示例:</h2>
<h3 id="模板" tabindex="-1"><a class="header-anchor" href="#模板" aria-hidden="true">#</a> 模板</h3>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>{{ first.DATA }}

商品明细：

名称：{{ name.DATA }}
价格：{{ price.DATA }}

{{ remark.DATA }}
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><p>发送模板消息：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$userId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'OPENID'</span><span class="token punctuation">;</span>
<span class="token variable">$templateId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'ngqIpbwh8bUfcSsECmogfXcV14J0tQlEpBO27izEYtY'</span><span class="token punctuation">;</span>
<span class="token variable">$url</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'http://overtrue.me'</span><span class="token punctuation">;</span>
<span class="token variable">$data</span> <span class="token operator">=</span> <span class="token keyword">array</span><span class="token punctuation">(</span>
         <span class="token string double-quoted-string">"first"</span>  <span class="token operator">=></span> <span class="token string double-quoted-string">"恭喜你购买成功！"</span><span class="token punctuation">,</span>
         <span class="token string double-quoted-string">"name"</span>   <span class="token operator">=></span> <span class="token string double-quoted-string">"巧克力"</span><span class="token punctuation">,</span>
         <span class="token string double-quoted-string">"price"</span>  <span class="token operator">=></span> <span class="token string double-quoted-string">"39.8元"</span><span class="token punctuation">,</span>
         <span class="token string double-quoted-string">"remark"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"欢迎再次购买！"</span><span class="token punctuation">,</span>
        <span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$notice</span><span class="token operator">-></span><span class="token function">uses</span><span class="token punctuation">(</span><span class="token variable">$templateId</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">withUrl</span><span class="token punctuation">(</span><span class="token variable">$url</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">andData</span><span class="token punctuation">(</span><span class="token variable">$data</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">andReceiver</span><span class="token punctuation">(</span><span class="token variable">$userId</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token function">var_dump</span><span class="token punctuation">(</span><span class="token variable">$result</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// {</span>
<span class="token comment">//      "errcode":0,</span>
<span class="token comment">//      "errmsg":"ok",</span>
<span class="token comment">//      "msgid":200228332</span>
<span class="token comment">//  }</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br></div></div><p>结果：</p>
<p><img src="http://7u2jwa.com1.z0.glb.clouddn.com/QQ20160111-0@2x.png" alt="notice-demo"></p>
<h2 id="模板数据" tabindex="-1"><a class="header-anchor" href="#模板数据" aria-hidden="true">#</a> 模板数据</h2>
<p>为了方便大家开发，我们拓展支持以下格式的模板数据，其它格式的数据可能会导致接口调用失败：</p>
<ul>
<li>
<p>所有数据项颜色一样的（这是方便的一种方式）:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$data</span> <span class="token operator">=</span> <span class="token keyword">array</span><span class="token punctuation">(</span>
    <span class="token string double-quoted-string">"first"</span>    <span class="token operator">=></span> <span class="token string double-quoted-string">"恭喜你购买成功！"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"keynote1"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"巧克力"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"keynote2"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"39.8元"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"keynote3"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"2014年9月16日"</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"remark"</span>   <span class="token operator">=></span> <span class="token string double-quoted-string">"欢迎再次购买！"</span><span class="token punctuation">,</span>
<span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><p>默认颜色为'#173177', 你可以通过 <code>defaultColor($color)</code> 来修改</p>
</li>
<li>
<p>独立设置每个模板项颜色的：</p>
<ul>
<li>
<p>简便型：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$data</span> <span class="token operator">=</span> <span class="token keyword">array</span><span class="token punctuation">(</span>
    <span class="token string double-quoted-string">"first"</span>    <span class="token operator">=></span> <span class="token keyword">array</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"恭喜你购买成功！"</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'#555555'</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"keynote1"</span> <span class="token operator">=></span> <span class="token keyword">array</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"巧克力"</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"#336699"</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"keynote2"</span> <span class="token operator">=></span> <span class="token keyword">array</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"39.8元"</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"#FF0000"</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"keynote3"</span> <span class="token operator">=></span> <span class="token keyword">array</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"2014年9月16日"</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"#888888"</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"remark"</span>   <span class="token operator">=></span> <span class="token keyword">array</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"欢迎再次购买！"</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"#5599FF"</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
<span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div></li>
<li>
<p>复杂型（也是微信官方唯一支持的方式，估计没有人想这么用）：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$data</span> <span class="token operator">=</span> <span class="token keyword">array</span><span class="token punctuation">(</span>
    <span class="token string double-quoted-string">"first"</span>    <span class="token operator">=></span> <span class="token keyword">array</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"value"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"恭喜你购买成功！"</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"color"</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'#555555'</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"keynote1"</span> <span class="token operator">=></span> <span class="token keyword">array</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"value"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"巧克力"</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"color"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"#336699"</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"keynote2"</span> <span class="token operator">=></span> <span class="token keyword">array</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"value"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"39.8元"</span><span class="token punctuation">,</span><span class="token string double-quoted-string">"color"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"#FF0000"</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"keynote3"</span> <span class="token operator">=></span> <span class="token keyword">array</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"value"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"2014年9月16日"</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"color"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"#888888"</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"remark"</span>   <span class="token operator">=></span> <span class="token keyword">array</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"value"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"欢迎再次购买！"</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"color"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"#5599FF"</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
<span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div></li>
</ul>
</li>
</ul>
<p>关于模板消息的使用请参考 <a href="http://mp.weixin.qq.com/wiki/" target="_blank" rel="noopener noreferrer">微信官方文档<OutboundLink/></a></p>
